/* components/ContactSectionUs.module.css */
.contactSection {
  padding: 4rem 2rem;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.contactHeader {
  text-align: center;
  margin-bottom: 3rem;
}

.companyName {
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  color: #2c3e50;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.tagline {
  font-size: 1.4rem;
  font-weight: 300;
  margin-bottom: 0.5rem;
  color: #7f8c8d;
  letter-spacing: 2px;
}

.description {
  color: #95a5a6;
  margin-bottom: 0;
  font-size: 1.1rem;
}

.contactContent {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  background: white;
  padding: 2.5rem;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}

@media (min-width: 992px) {
  .contactContent {
    grid-template-columns: 1fr 1.2fr;
  }
}

.contactInfo h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: #2c3e50;
  position: relative;
  padding-bottom: 10px;
}

.contactInfo h2::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background: #3498db;
}

.contactInfo ul {
  list-style: none;
  padding: 0;
}

.contactInfo li {
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
  color: #34495e;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 6px;
  transition: all 0.3s ease;
}

.contactInfo li:hover {
  background: #e9ecef;
  transform: translateX(5px);
}

.contactInfo strong {
  margin-bottom: 0.4rem;
  color: #2c3e50;
  font-size: 0.9rem;
  letter-spacing: 1px;
}

.formGroup {
  margin-bottom: 1.8rem;
}

.formGroup label {
  display: block;
  margin-bottom: 0.6rem;
  font-weight: 600;
  color: #2c3e50;
  font-size: 1rem;
}

.formGroup input,
.formGroup textarea {
  width: 100%;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 1rem;
  transition: all 0.3s ease;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.formGroup input:focus,
.formGroup textarea:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}

.formGroup textarea {
  min-height: 140px;
  resize: vertical;
}

.phoneInput {
  display: flex;
  align-items: center;
}

.phonePrefix {
  padding: 1rem;
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  border-right: none;
  border-radius: 6px 0 0 6px;
  color: #7f8c8d;
  font-weight: 500;
}

.phoneInput input {
  border-radius: 0 6px 6px 0;
  flex: 1;
}

.submitBtn {
  background: #2c3e50;
  color: white;
  padding: 1.2rem 2.5rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 600;
  transition: all 0.3s ease;
  width: 100%;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.submitBtn:hover {
  background: #3498db;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(52, 152, 219, 0.3);
}

.successMessage {
  background: #d4edda;
  color: #155724;
  padding: 1rem;
  border-radius: 6px;
  margin-top: 1.5rem;
  text-align: center;
  font-weight: 500;
}

.errorMessage {
  background: #f8d7da;
  color: #721c24;
  padding: 1rem;
  border-radius: 6px;
  margin-top: 1.5rem;
  text-align: center;
  font-weight: 500;
}
